import themeMixin from 'kolibri.coreVue.mixins.themeMixin';
import KButton from 'kolibri.coreVue.components.KButton';

export default {
  name: 'QuestionList',
  components: {
    KButton,
  },
  mixins: [themeMixin],
  props: {
    questions: {
      type: Array,
      required: true,
    },
    selectedIndex: {
      type: Number,
      required: true,
    },
    questionLabel: {
      type: Function,
      required: true,
      // simple validator, makes sure the function returns a string
      validator: value => typeof value(0) === 'string',
    },
  },
  methods: {
    buttonStyle(index) {
      return {
        backgroundColor: index === this.selectedIndex ? this.$themeColors.palette.grey.v_300 : '',
      };
    },
  },
  $trs: {
    questionListHeader: '{numOfQuestions, number} Questions',
    questionLabel: 'Question { questionNumber, number }',
  },
};

