import KEmptyPlaceholder from 'kolibri.coreVue.components.KEmptyPlaceholder';
import { coachStringsMixin } from './commonCoachStrings';

export default {
  name: 'Score',
  components: {
    KEmptyPlaceholder,
  },
  mixins: [coachStringsMixin],
  props: {
    value: {
      type: Number,
      required: false,
      validator(value) {
        return value >= 0 && value <= 1;
      },
    },
  },
};

